import { Component, OnInit } from '@angular/core';
import { ActivityStack } from '../services/activity-stack.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { User } from '../services/user.service';
import { DialogClose } from '../dialog/dialog-close';

@Component({
    selector: 'app-shop-pane',
    templateUrl: './shop-pane.component.html',
    styleUrls: ['./shop-pane.component.css']
})
export class ShopPaneComponent implements OnInit{
    
    //用户头像
    headImg:string = ""

    constructor(
        private stack:ActivityStack,
        private location:Location,
        private router:Router,
        private user:User
    ) { 
    }
    
    ngOnInit() {
        //获取用户信息
        this.user.directPushListener(() => {
            this.headImg = this.user.headImg
        },true)
    }
    
    //返回上一级
    back(){
        this.stack.popURL()
        this.router.navigate([this.stack.peek()])
    }
}



