import { Injectable } from "@angular/core";

/**
 * 功能：维护一个执行栈，管理组件的访问
 */

@Injectable()
export class ActivityStack{
    //执行栈
    private stack:string[] = []

    //访问执行栈
    getStack():string[]{
        return this.stack
    }

    //压入url到执行栈
    pushURL(url:string){
        //防止相邻的两个栈帧重复
        if(this.peek() == url) return
        //入栈
        this.stack.push(url)
    }

    //弹出url
    popURL():string{
        return this.stack.pop()
    }

    //访问栈顶元素，不弹出
    peek():string{
        return this.stack[this.stack.length - 1]
    }
    
    //重置执行栈
    reset(){
        this.stack = []
    }
}


