import { Organize2User } from "../interfaces/organize-user.interface";
import { Http, Response } from "@angular/http";
import { User } from "./user.service";
import { Injectable } from "@angular/core";
import { ConfigService } from "./config.service";
import { Observable } from "rxjs/Observable";

/**
 * 组队匹配第二版
 */

@Injectable()
export class Organize2{
    //组队的用户列表
    private userArr:Organize2User[] = []

    //监听者队列
    listeners:Function[] = []
    
    //是否为队伍的创始人
    private _isCaptain:boolean = false

    constructor(
        private http:Http,
        private user:User,
        private configService:ConfigService
    ){

    }
    
    set isCaptain(isCaptain:boolean){
        this._isCaptain = isCaptain
    }
    get isCaptain() {
        return this._isCaptain
    }

    //初始化用户列表
    init(userArr:Organize2User[]){
        this.userArr = userArr
    }

    //添加用户
    addUser(cb?:Function){
        this.addAndRemove("/addUserInOrganize2")
            .subscribe((res:Response) => {
                this.userArr = res.json().organize
                this.evalutedListener()
                console.log(this.userArr)
                if(cb) cb()
            })
    }

    //删除用户
    removeUser(cb?:Function){
        this.addAndRemove("/removeUserInOrganize2")
            .subscribe((res:Response) => {
                this.userArr = res.json().organize
                this.evalutedListener()
                if(cb) cb()
            })
    }
    
    //创建队伍 自己为队长
    createOrganize(cb?:Function){
        this.http.post(this.configService.exportInterface("/createOrganize2"),{
            captain:{
                user:this.user.user,
                headImg:this.user.headImg
            }
        })
        .subscribe((res:Response) => {
            let data:Organize2User[] = res.json().organize
            this.userArr = data
            if(cb) cb()
        })
    }
    
    //销毁队伍
    dropOrganize(){
        this.http.post(this.configService.exportInterface("/dropOrganize"),{user:{user:this.user.user}})
            .subscribe(() => {})
    }

    //获取队伍数组
    getOrganizeArr(){
        return this.userArr
    }

    private addAndRemove(tag:string):Observable<Response>{
        if(this.userArr.length == 0) throw new Error("请先初始化用户列表")
        return this.http.post(this.configService.exportInterface(tag),{
            captain:this.userArr[0],
            user:{
                user:this.user.user,
                headImg:this.user.headImg
            }
        })
    }

    //添加监听者
    addListener(func:Function){
        this.listeners.push(func)
    }

    //遍历监听者执行
    evalutedListener(){
        for(let func of this.listeners){
            func()
        }
    }
    
    isMatch(isMatch:boolean){
        this.http.post(this.configService.exportInterface("/organizeMatch"),{user:this.userArr[0].user,isMatch:isMatch})
            .subscribe(() => {})
    }

    //组队匹配
    match(){
        this.isMatch(true)
    }
    
    removeOrganizeToMatch(){
        this.isMatch(false)
    }
}


