import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivityStack } from '../services/activity-stack.service';
import { Location } from '@angular/common';
import { ProductModel } from '../interfaces/product.interface';
import { ProductService } from '../services/product.service';
import { SubscribeCollection } from '../services/subscribe-collection';
import { Response } from '@angular/http';
import { HeadImgPipe } from '../pipes/headImgPipe.pipe';
import { DialogClose } from '../dialog/dialog-close';
import { User } from '../services/user.service';

@Component({
    selector: 'app-product',
    templateUrl: './product.component.html',
    styleUrls: ['./product.component.css']
})
export class ProductComponent extends DialogClose implements OnInit,OnDestroy {
    constructor(
        private stack:ActivityStack,
        private location:Location,
        private productService:ProductService,
        private subscribeCollection:SubscribeCollection,
        private user:User
    ) { 
        super()
    }
    
    products:ProductModel[] = []

    ngOnInit() {
        //入栈
        this.stack.pushURL(this.location.path())

        this.subscribeCollection.addSubscription("getProducts", 
            this.productService.getProducts()
            .subscribe((res:Response) => {
                let data:ProductModel[] = res.json().products
                this.products = data
            })
        )
        
    }
    
    
    //购买商品
    buyProduct(name:string,price:number){
        console.log(name,price)
        //检查是否购买重复的商品
        for(let t of this.user.tools){
            if(t.name == name){
                this.basicDialog("请不要购买重复的商品","red")
                return
            }
        }
        
        //询问是否确认购买
        this.showDialog({
            title:"提示",
            message:`是否花费 ${price} 元购买 ${name} ？`,
            buttonIndex:2,
            btn1:{title:"确认",color:"green",func:() => {
                this.subscribeCollection.addSubscription("buyProduct",
                    this.productService.buyProduct(name, price)
                    .subscribe((res:Response) => {
                        let data = res.json()
                        console.log(data)
                        if(data.err) this.basicDialog(data.err,"red")
                        else {
                            this.basicDialog("付款成功")
                            this.user.updateUserProperty(true)
                        }
                    })
                )
            }},
            btn2:{title:"算了",color:"red",func:() => {}}
        })
    }

    ngOnDestroy(): void {
        this.subscribeCollection.unSubscription("buyProduct")
        this.subscribeCollection.unSubscription("getProducts")
    }
}



