import { SubscribeCollection } from './../../services/subscribe-collection';
import { User } from './../../services/user.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ConfigService } from '../../services/config.service';
import { Msg2Service } from '../../services/msg2.service';

@Component({
  selector: 'app-header-bar',
  templateUrl: './header-bar.component.html',
  styleUrls: ['./header-bar.component.css']
})
export class HeaderBarComponent implements OnInit, OnDestroy {
    //需要显示的用户名
    userName:string;
    //未读消息数量
    msgCount:number = 0;
    //金币数量
    money:number = 0;
    //头像路径
    headImg:string = ""
    //显示积分
    pointView:number = 0
    //等级
    level:number = 0
    
    width:number = 10

    constructor(
        private user:User,
        private router:Router, 
        private msg2Service:Msg2Service,
        private subscribeCollection:SubscribeCollection,
        private activatedRoute:ActivatedRoute,
        private configService:ConfigService
    ) { 
    
    }

  ngOnInit() {

    let obj = this.user.getUserInfo();
    this.userName = obj.user;
    
    //更新用户信息
    this.user.directPushListener(() => {
        this.money = this.user.money
        this.headImg = this.configService.exportInterface(this.user.headImg)
        this.pointView = this.user.pointView
        this.level = this.user.level
        this.width = (parseFloat(this.pointView + '') / parseFloat(this.level * 15 + '')) * 100
    })

    //接收未读消息数量
    let addListenerFunc = () => {
        this.msgCount = this.msg2Service.getInnerMsgCount()
        console.log(this.msgCount)
    }
    this.msg2Service.addListener(addListenerFunc,true)
  }

  //转到好友列表
  linkFriendList(){
    this.router.navigate(['main/friendList']);
  }

  //注销用户
  exitUser(){
    this.router.navigate(['entry']);
  }

  //跳转到msg-pane
  linkMsgPane(){
    this.router.navigate(['main/msg-pane']);
  }

  ngOnDestroy(){
    this.subscribeCollection.unSubscription("getMsgCount");
  }

  //用户头像点击修改
  updateUserInfo(){
    this.router.navigate(['user-info'],{relativeTo:this.activatedRoute})
  }
}



