import { Http, Response } from '@angular/http';
import { Location } from '@angular/common';
import { DialogClose } from './../dialog/dialog-close';
import { ChatService } from './../services/chat.service';
import { User } from './../services/user.service';
import { LiteFriendList } from './../liteFriendList/lite-friend-list.component';
import { FriendInfoComponent } from './../main/main-tab/friendList/friendInfo/friend-info.component';
import { Component, OnInit, AfterViewInit, OnDestroy, OnChanges } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ActivityStack } from '../services/activity-stack.service';
import { Organize2 } from '../services/organize2.service';
import { Organize2User } from '../interfaces/organize-user.interface';
import { MsgModel } from '../interfaces/msg-dto.interface';
import { ConfigService } from '../services/config.service';
import { FriendGamePipe } from '../pipes/friendGamePipe.pipe';
import { SubscribeCollection } from '../services/subscribe-collection';

//用户信息
interface UserInfo{
    headImg?:string;
    userName:string
}

@Component({
    selector:"friend-game",
    templateUrl:"./friend-game.component.html",
    styleUrls:["./friend-game.component.css"]
})
export class FriendGame extends DialogClose implements OnInit,AfterViewInit,OnDestroy{
    //组队用户信息
    userInfo:Array<UserInfo> = [];    
    //用户名数组
    userNameInfo:Array<string>;
    //用户默认头像
    headImgPath:string = "../../assets/imgs/headImg.jpg";
    //当前用户人数
    userIndex:number = 0;
    //服务器通知更新的订阅对象数组
    subScription:Array<any> = [];
    
    //组队用户信息对象
    organizeArr:Organize2User[]
    
    //当前用户名
    username:string = ""


    constructor(
        private router:Router,
        private user:User, 
        private chatService:ChatService, 
        private activatedRoute:ActivatedRoute,
        private http:Http,
        private stack:ActivityStack,
        private location:Location,
        private organizeService:Organize2,
        private configService:ConfigService,
        private subscribeCollection:SubscribeCollection
    ){
        super();
        
        this.organizeArr = this.organizeService.getOrganizeArr()
        //添加进监听者队列
        this.organizeService.addListener(() => this.organizeArr = this.organizeService.getOrganizeArr())
        
        console.log("getMsg")
        //更新队伍消息
        subscribeCollection.addSubscription("friendGameGetMsg",this.chatService.getMsg()
        .subscribe((data:{msg:MsgModel, tag:string}) => {
            //更新队伍消息
            if(data.tag == "updateOrganize"){
                let body = data.msg.body
                this.organizeArr = body as any
                console.log(`${this.username}   ${this.organizeArr[0].user}`)
            }
                
            //销毁队伍 队长已推出 队伍解散
            if(data.tag == "dropOrganize"){
                this.showDialog({
                    title:"提示",
                    message:"队伍已经解散",
                    buttonIndex:1,
                    btn1:{
                        title:"确认",
                        color:"red",
                        func:() => {
                            this.stack.popURL()
                            this.router.navigate([this.stack.peek()])
                        }
                    }
                })
            }

            //通知开始匹配
            if(data.tag == "Matchtrue"){
                console.log("匹配开始！")
                this.dialogInit()
            }

            //通知取消匹配
            if(data.tag == "Matchfalse"){
                console.log("匹配结束！")
                this.close()
            }
        }))

        //更新用户空闲状态
        this.user.setFreeState(false)
        .subscribe((res:Response) => {
            console.log("update user isFree property");
        });
        
        //更新用户名
        this.username = this.user.user
    }  

    //发送请求给好友
    sendRequestToFriend(targetUser:string){
        this.user.getFreeState(targetUser)
        .subscribe((data:any) => {
            if(data.isFree && data.isLine){
                //发送请求
                // this.chatService.sendRequestToFriend(targetUser);
                this.user.sendMsg(targetUser,this.organizeService.getOrganizeArr(),"organizeRequest")
            }
            else if(!data.isLine){
                this.basicDialog("该用户处于离线状态");
            }
            else{
                this.basicDialog("该用户不在空闲状态");
            }
        });
    }

    //初始化钩子
    ngOnInit(){
        //url入栈
        this.stack.pushURL(this.location.path())

        this.subScription.push(this.chatService.updateInfo()
        .subscribe((info:any) => {
            //判断是否要销毁队伍
            if(info.isDrop){
                this.showDialog({
                    title:"提示信息",
                    message:"队伍已解散",
                    buttonIndex:1,
                    btn1:{title:"确定",color:"green",func:() => {
                        this.close();
                        this.back();                        
                    }}
                });
                return;
            }

            let data = info.organize;
            let arr = [];
            arr.push({userName:data.first,headImg:this.headImgPath});
            for(let val of data.users){
                arr.push({userName:val,headImg:this.headImgPath});
            }           
            let temp = 5 - arr.length;
            //补全数据
            for(let i = 0;i < temp;i++){
                arr.push({userName:'',headImg:''});
            }
                
            let userNameInfo = data.users;
            userNameInfo.push(data.first);
            this.userNameInfo = userNameInfo;

            this.userInfo = arr;
        }));

        this.subScription.push(this.chatService.matchSuccessful()
        .subscribe(data => {
            if(data){
                //关闭对话框
                this.close();
                //匹配成功后打开主页面
                console.log("匹配成功");
                this.router.navigate(['main/game-pane']);
            }
        }));
    }

    back(){
        //url出栈
        this.stack.popURL()
        this.router.navigate([this.stack.peek()]);
    }

    ngAfterViewInit(){
        
    }
    ngOnDestroy(){
        //取消订阅
        for(let val of this.subScription){
            val.unsubscribe();
        }
        
        //判断是否自己为队长
        if(this.user.user == this.organizeArr[0].user) {
            this.organizeService.dropOrganize()
        } else {
            //退出队伍
            this.organizeService.removeUser(() => this.organizeArr = this.organizeService.getOrganizeArr())
        }
        
        //组件销毁后全部取消订阅
        this.subscribeCollection.unSubscription("friendGameGetMsg")

        //设置用户空闲状态
        this.user.setFreeState(true)
        .subscribe((res:Response) => {
            console.log("update user isFree property");
        })
    }

    //配置dialogmodel
    dialogInit(){
        this.showDialog({
            title:"提示信息",
            message:"匹配中...",
            buttonIndex:0,
            isViewImg:true
        });
    }

    //匹配
    match(){
        this.dialogInit();
        //超时设置
        setTimeout(() => {
            //显示取消匹配按钮
            this.dialogModel.cancelMatchIsView = true
            this.dialogModel.cancelMatchFunc = () => {
                this.close()
                //将用户从匹配队列中剔除
                this.organizeService.removeOrganizeToMatch()
            }
            
        },this.configService.MAX_MATCH_TIME)
        
        console.log(this.userNameInfo);
        this.organizeService.match()
    }
}


