import { Component, OnInit, Input } from '@angular/core';
import { ConfigService } from '../services/config.service';
import { User } from '../services/user.service';
import { ChatService } from '../services/chat.service';

@Component({
    selector: 'app-ready-pane',
    templateUrl: './ready-pane.component.html',
    styleUrls: ['./ready-pane.component.css']
})
export class ReadyPaneComponent implements OnInit {
    @Input()
    readyQueue:{headImg:string, user:string}[]

    //确认按钮点击样式变换
    isClick:boolean = false
    //是否可以点击准备按钮
    isLock:boolean = true

    constructor(
        private configService:ConfigService,
        private user:User,
        private chatService:ChatService
    ) { }

    ngOnInit() {
        
    }

    confirm(){
        if(this.isLock){
            this.isClick = true
            this.chatService.updateReadyQueue()
            this.isLock = false
        }
    }
}



