import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { User } from '../services/user.service';
import { ConfigService } from '../services/config.service';
import { ActivityStack } from '../services/activity-stack.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { DialogClose } from '../dialog/dialog-close';
import { Response } from '@angular/http';

@Component({
    selector: 'app-update-head-img',
    templateUrl: './update-head-img.component.html',
    styleUrls: ['./update-head-img.component.css']
})
export class UpdateHeadImgComponent extends DialogClose implements OnInit {
    //头像访问uri
    headImg:string = ""
    //上传的文件实体
    fileEntity:File = null

    constructor(
        private user:User,
        private config:ConfigService,
        private stack:ActivityStack,
        private location:Location,
        private router:Router,
        private configService:ConfigService
    ) {
        super();
    }

    ngOnInit() {
        //入栈
        this.stack.pushURL(this.location.path())
        this.headImg = this.configService.exportInterface(this.user.headImg)
    }
    
    //返回上一级
    back(){
        this.stack.popURL()
        this.router.navigate([this.stack.peek()])
    }
    
    @ViewChild("file")
    file:ElementRef
    //选择图片
    selectHeadImg(){
        let f:HTMLInputElement = this.file.nativeElement   
        //选择文件
        f.click()
    }
    //上传头像
    uploadHeadImg(){
        if(!this.fileEntity){
            this.basicDialog("请先选择图片")
            return
        }
        let formData = new FormData()
        formData.append("headImg",this.fileEntity)
        formData.append("user",this.user.getUserInfo().user)
        //提交头像到后端
        this.user.uploadHeadImg(formData)
            .subscribe((res:Response) => {
                let data = res.json()
                this.user.updateUserProperty(true)
                this.basicDialog("上传成功")
            })
    }
    //上传文件回调
    fileChange(){
        //判断是否选中的为图片
        let imgRegex = /^image\/.*$/
        let f:HTMLInputElement = this.file.nativeElement
        let fileEntity = f.files[0]
        if(!imgRegex.test(fileEntity.type)) {
            this.basicDialog("请上传一个图片")
            return
        }
        
        let fileReader = new FileReader()
        fileReader.onloadend = () => {
            //更新图片
            this.headImg = fileReader.result
            this.fileEntity = fileEntity
        }
        
        //读取图片
        if(fileEntity) fileReader.readAsDataURL(fileEntity)
        console.log(fileEntity)
    }
}



