import { Router } from '@angular/router';
import { DialogComponent } from './../dialog/dialog.component';
import { DialogClose } from './../dialog/dialog-close';
import { DialogModelSchema } from './../interfaces/dialog-schema.interface';
import { Observable } from 'rxjs/Observable';
import { ChatService } from './../services/chat.service';
import { Http, Response } from '@angular/http';
import { Component, ElementRef, ViewChild, AfterViewInit, OnInit, EventEmitter, OnDestroy } from '@angular/core';
import { ReadyQueue } from '../interfaces/ready-queue.interface';
import { GameDTO } from '../interfaces/game-dto.interface';
import { Point } from '../services/point.service';
import { User } from '../services/user.service';
import { ConfigService } from '../services/config.service';
import { SubscribeCollection } from '../services/subscribe-collection';
import { HeadImgPipe } from '../pipes/headImgPipe.pipe';
import { ProductModel } from '../interfaces/product.interface';

//颜色关联数组
const Color = {
    red:"#ff0000",
    orange:"#ff8c00",
    yellow:"#ffff00",
    olive : "#9acd32",
    green : "#32cd32",
    teal : "#7fffd4",
    blue : "#1e90ff",
    violet : "#8a2be2",
    purple : "#ba55d3",
    pink : "#ff00ff",
    brown : "#CD853F",
    grey : "#a9a9a9",
    black : "#000000"
}

//坐标接口
interface Pos{
    x:number;
    y:number;
}

//获取触摸x y坐标
function getPosition(e:TouchEvent,canvasDOM:HTMLCanvasElement){
    let touched = e.changedTouches;
    let x:number = touched[0].clientX - canvasDOM.offsetLeft;
    let y:number = touched[0].clientY - canvasDOM.offsetTop;
    return {x:x,y:y};
}

//更改isRight属性
function changeIsRight(readyQueue:ReadyQueue[], gameDTO:GameDTO){
    for(let obj of readyQueue){
        if(obj.user == gameDTO.user){
            obj.body = gameDTO.body
            obj.isRight = gameDTO.isRight
            obj.isMsgView = true
            if(!obj.isRight) {
                setTimeout(() => {
                    obj.isMsgView = false
                    obj.body = ""
                },2000)  
            }
        }
    }
    return readyQueue
}

//重置用户队列
function resetUserQueue(readyQueue:ReadyQueue[]){
    for(let obj of readyQueue){
        obj.isRight = false
        obj.body = ""
        obj.isMsgView = false
    }
    return readyQueue
}

//更新isHost字段 更新画图者
function updateDrawer(readyQueue:ReadyQueue[], user:string){
    for(let obj of readyQueue){
        if(obj.user == user) obj.isHost = true
        else obj.isHost = false
    }
    return readyQueue
}

@Component({
    selector:"game-pane",
    templateUrl:"game-pane.component.html",
    styleUrls:["game-pane.component.css"]
})
export class GamePaneComponent extends DialogClose implements AfterViewInit,OnInit,OnDestroy{
    //canvas DOM 对象
    canvasDOM:HTMLCanvasElement;
    //canvas2d DOM 对象
    canvas2dDOM:CanvasRenderingContext2D;
    //canvas elementRef
    @ViewChild("canvas")
    canvas:ElementRef;
    //content elementRef
    @ViewChild("content")
    content:ElementRef;
    //回答问题的面板的用户
    chatUsers:ReadyQueue[] = this.chatService.readyQueue
    
    //画图锁 是否能够画图
    drawLock:boolean = true;

    //当前线条颜色
    lineColor:string = "#000";
    
    //黑板初始化
    isBlackboardInit:boolean = false;
    //黑板是否已经初始化过了
    isBlackboardInited:boolean = false;
    
    //等待队列
    waitQueue:Function[] = []

    //当前作图用户
    drawer:string

    //时间进度条的宽度 (%)
    progWidth:number = 100

    //显示答案的图像
    answerImg:string
    
    //是否显示答案面板
    isAnswerPaneView:boolean = false

    //正确答案
    rightAnswer:string = ""

    //工具集
    tools:ProductModel[] = []

    constructor(
        private chatService:ChatService,
        private http:Http,
        private router:Router,
        private pointService:Point,
        private user:User,
        private configService:ConfigService,
        private subscribeCollection:SubscribeCollection
    ){
        super();
    }
    
    //初始化钩子
    ngOnInit(){
        //开始游戏
        this.chatService.beginGame();

        //结束游戏
        this.chatService.gameOver()
        .subscribe(() => {
            this.isAnswerPaneView = false
            console.log("游戏结束");
            clearInterval(this.timingID)
            //添加至事件队列尾部
            setTimeout(() => {
                this.showDialog({
                    title:"提示信息",
                    message:"游戏已经结束",
                    buttonIndex:1,
                    btn1:{title:"退出房间",func:() => {
                        this.router.navigate(['main']);
                        this.close();
                        //结算分数
                        this.pointService.updatePoint()
                        this.pointService.updateMoney()
                        //更新用户信息
                        this.user.updateUserProperty(true)
                    },color:"red"}
                });
            },10);
        });

        //获取题目
        this.chatService.getQuestion()
        .subscribe((data:string) => {
            this.isAnswerPaneView = false
            let tmp = () => {
                //显示对话框
                this.showDialog({
                    title:"游戏时间",
                    message:`题目 : ${data}`,
                    buttonIndex:1,
                    btn1:{title:"确定",func:() => {
                        this.close();
                    }}
                });
            }
            if(!this.isBlackboardInited) this.waitQueue.push(tmp)
            else tmp()
        });
    
        //接受服务器通知是否答对
        this.subscribeCollection.addSubscription("validAnswerResult",this.chatService.validAnswerResult()
        .subscribe((data:GameDTO) => {
            switch(data.tag){
                //回答问题
                case "rightAndError":
                    console.log(data)
                    this.chatUsers = changeIsRight(this.chatUsers,data)    
                    //自己画别人答对加积分
                    if(this.user.user != data.user) {
                        console.log(`自己主场增加金币 ${this.pointService.HOST_MONEY}`)
                        this.pointService.addPointCache(this.pointService.HOST_POINT)
                        this.pointService.addMoneyCache(this.pointService.HOST_MONEY)
                    }
                    else {
                        console.log(`别人主场增加金币 ${this.pointService.BY_AWAY_MONEY}`)
                        this.pointService.addPointCache(this.pointService.BY_AWAY_POINT)
                        this.pointService.addMoneyCache(this.pointService.BY_AWAY_MONEY)
                    }
                    break;
                //全部正确
                case "all-right":
                    this.viewAnswerPane()
                    this.chatUsers = resetUserQueue(this.chatUsers)
                    this.chatUsers = updateDrawer(this.chatUsers,this.drawer)
                    console.log("全部答对")
                    break;
                //重复答题
                case "repeat-answer":
                    this.basicDialog("请勿重复作答","red");
                    break;
                //更新画图者
                case "updateDrawer":
                    this.drawer = data.user
                    this.chatUsers = updateDrawer(this.chatUsers,this.drawer)
                    //开始计时
                    this.timing()
                    this.isAnswerPaneView = false
                    break;
                //时间结束
                case "currentOver":
                    console.log("时间结束")
                    this.chatUsers = resetUserQueue(this.chatUsers)
                    this.chatUsers = updateDrawer(this.chatUsers,this.drawer)
                    this.viewAnswerPane()
                    break;
                default:
                    throw new Error("错误值传递 （答案验证）");
            }
        }))

        //服务器通知可以绘画
        this.chatService.canDraw()
        .subscribe((data:boolean) => {
            this.drawLock = !data;
            //清空画布
            this.canvas2dDOM.clearRect(0,0,parseFloat(this.canvasDOM.getAttribute("width")),parseFloat(this.canvasDOM.getAttribute("height")));
        })

        this.tools = this.user.tools.filter(f => f.price != -1)
   
    }

    //子视图渲染完毕钩子
    ngAfterViewInit(){
        //初始化黑板
        this.isBlackboardInit = true
        setTimeout(() => {
            this.isBlackboardInit = false;
            if(this.waitQueue[this.waitQueue.length - 1]) this.waitQueue.pop()()
            this.isBlackboardInited = true
        },1000)

        //获取canvas2d对象
        this.canvasDOM = this.canvas.nativeElement;
        this.canvas2dDOM = this.canvasDOM.getContext('2d');
        let canvas2dDOM = this.canvas2dDOM;
        //设置canvas宽高
        this.canvasDOM.setAttribute("width",window.getComputedStyle(this.content.nativeElement)["width"]);
        this.canvasDOM.setAttribute("height",window.getComputedStyle(this.content.nativeElement)["height"]);

        //设置线条样式
        this.canvas2dDOM.lineWidth = 0.5;
        this.canvas2dDOM.strokeStyle = "#000";
        
        //监听触摸事件
        this.canvasDOM.addEventListener("touchstart",(e:any) => {
            if(this.drawLock) return;
            this.canvas2dDOM.beginPath();
            // this.canvas2dDOM.clearRect(0,0,parseFloat(this.canvasDOM.getAttribute("width")),parseFloat(this.canvasDOM.getAttribute("height")));
            let posObj:Pos = getPosition(e,this.canvasDOM);
            this.canvas2dDOM.moveTo(posObj.x,posObj.y);
        });
        this.canvasDOM.addEventListener("touchmove",(e:any) => {
            if(this.drawLock) return;
            let posObj:Pos = getPosition(e,this.canvasDOM);
            this.canvas2dDOM.lineTo(posObj.x,posObj.y);
            this.canvas2dDOM.stroke();

            this.chatService.changeCanvas(this.canvasDOM.toDataURL());
        });
        this.canvasDOM.addEventListener("touchend",e => {
            this.canvas2dDOM.closePath();
            this.answerImg = this.canvasDOM.toDataURL()
        });

        //服务器通知更新canvas
        let updateCanvas:Observable<string> = this.chatService.getUpdateCanvas();
        let img:any = new Image();
        updateCanvas.subscribe((dataURL:string) => {
            img.src = dataURL;
            img.onload = function(){
                canvas2dDOM.drawImage(img,0,0);
            }
            this.answerImg = dataURL
            this.answerImg = this.canvasDOM.toDataURL()
        });
    }

    popAnswer(){
        //不能答题
        if(!this.drawLock){
            this.showDialog({
                title:"提示信息",
                message:"现在是您的游戏时间，不能作答",
                buttonIndex:1,
                btn1:{title:"确定",func:() => {
                    this.close();
                }}
            });
            return;
        }
        this.showDialog({
            title:"提示信息",
            message:"",
            buttonIndex:1,
            btn1:{title:"确定",func:(data:string) => {
                if(data == "" || !data) return
                this.chatService.validAnswer(data);
                //关闭对话框
                this.close();
            }},
            isInputView:true
        });
    }


    getInputValue(data:any){
        console.log(data);
    }

    //更改线条样式
    changeLine(event:any,target:string){
        let top:number;
        if(event.target.tagName == 'IMG') {
            //获取父元素的顶部高度
            top = event.target.parentNode.offsetTop;           
        }
        else{
            top = event.target.offsetTop;
        }
        // this.toolSelectorLocation = top;
        switch(target){
            case "pencil":
                this.canvas2dDOM.lineWidth = 0.5;
                this.canvas2dDOM.strokeStyle = this.lineColor;
            break;
            case "eraser":
                this.canvas2dDOM.lineWidth = 20;
                this.canvas2dDOM.strokeStyle = "#fff";
            break;
            case "rainbow":
                let gradient = this.canvas2dDOM.createLinearGradient(0,0,parseFloat(this.canvasDOM.getAttribute("width")),0);
                gradient.addColorStop(0,Color.red);
                gradient.addColorStop(0.14,Color.orange);
                gradient.addColorStop(0.28,Color.yellow);
                gradient.addColorStop(0.42,Color.green);
                gradient.addColorStop(0.56,Color.violet);
                gradient.addColorStop(0.7,Color.blue);
                gradient.addColorStop(0.84,Color.purple);

                // 用渐变进行填充
                this.canvas2dDOM.lineWidth = 8;
                this.canvas2dDOM.strokeStyle = gradient;
            break;
            case "make":
                this.canvas2dDOM.lineWidth = 10;
                this.canvas2dDOM.strokeStyle = this.lineColor;
            break;
            case "hollow":
                this.canvas2dDOM.lineWidth = 0.5
                this.canvas2dDOM.strokeStyle = "#000"
                this.canvas2dDOM.shadowColor = "#000"
                this.canvas2dDOM.shadowOffsetX = 2
                this.canvas2dDOM.shadowOffsetY = 2
            break;
        }

        this.isBackpackPop = false
        this.isColorSelectorPop = false
    }

    //更改线条颜色
    changeColor(color:string){
        this.lineColor = Color[color]
        this.canvas2dDOM.strokeStyle = this.lineColor
        this.isBackpackPop = false
        this.isColorSelectorPop = false
    }

    //开始计时
    timingID:NodeJS.Timer | undefined
    private timing(){
        if(this.timing) clearInterval(this.timingID)
        this.isAnswerPaneView = false
        //开始倒计时
        let currentTime = this.configService.MAX_TIME
        this.progWidth = 100
        this.timingID = setInterval(() => {
            currentTime -= 0.05
            this.progWidth = (currentTime / this.configService.MAX_TIME) * 100
        },50)
    }

    //显示答案面板
    private viewAnswerPane(){
        //获取正确答案
        this.chatService.getRightAnswer()
            .subscribe((res:Response) => {
                let data = res.json().rightAnswer
                this.rightAnswer = data
                this.isAnswerPaneView = true
                clearInterval(this.timingID)
            })
    }

    ngOnDestroy(): void {
        this.subscribeCollection.unSubscription("validAnswerResult")
    }
    
    
    isBackpackPop:boolean = false
    //背包按钮点击
    backpack(){
        this.isBackpackPop = true
        this.isColorSelectorPop = false
        console.log("backpack")
    }
    
    isColorSelectorPop:boolean = false
    //颜色选择器按钮点击
    colorSelector(){
        this.isColorSelectorPop = true
        this.isBackpackPop = false
        console.log("colorSelector")
    }
}


