export class ConfigService{
    public readonly PROTOCAL:string = "http" //使用的协议

    public readonly SERVER_HOST:string = "localhost" //服务器主机地址
    public readonly SERVER_PORT:string = "3000" //服务器端口
    
    public readonly AGE_ARR:number[] = [] //用于显示的年龄数组
    
    public readonly MAX_MATCH_TIME:number = 5000 //最大匹配超时时间
    
    public readonly MAX_ORGANIZE_USER_COUNT:number = 4 //组队最大人数

    public readonly LEVEL_INCREMENT = 15 //等级增量
    
    public readonly MAX_TIME = 60 //绘画的时候最大超时时间

    constructor(){
        //初始化一个202大小的数组，保证整个应用只初始化一次
        for(let i = -1;i <= 200;i++){
            this.AGE_ARR.push(i)
        }
    }

    //生成访问接口
    public exportInterface(path?:string):string{
        return `${this.PROTOCAL}://${this.SERVER_HOST}:${this.SERVER_PORT}${path || ""}`
    }
}


