import { Injectable } from "@angular/core";
import { chatUser } from "../interfaces/chat-user-schema.interface";
import { Http, Response } from "@angular/http";
import { ConfigService } from "./config.service";
import { Msg, MsgBody } from "../interfaces/msg.interface";
import { Observable } from "rxjs/Observable";
import { Observer } from "_rxjs@5.5.10@rxjs/Observer";

@Injectable()
export class Msg2Service{
    //储存聊天用户的容器
    private chatUsers:chatUser[] = []
    
    //聊天消息容器
    private msgCollections:Msg = {}
    
    //内部消息数目
    private _innerMsg:{[user:string]:number} = {}
    
    //当前选择用户
    private _selectedUserName:string = "空气"

    //监听者队列
    private listeners:Function[] = []
    
    set selectedUserName(value:string){
        this._selectedUserName = value
        this.executeListener()
    }
    get selectedUserName(){
        return this._selectedUserName
    }
    
    //添加未读消息
    addInnerMsg(user:string){
        if(!this._innerMsg.hasOwnProperty(user)) this._innerMsg[user] = 0
        this._innerMsg[user]++
        this.executeListener()
    }
    //获取未读消息对象
    getInnerMsg(){
        return this._innerMsg
    }
    //获取未读消息总数
    getInnerMsgCount(){
        let count = 0
        for(let index in this._innerMsg){
            count += this._innerMsg[index]
        }
        return count
    }
    //清空指定用户的消息数量
    clearUserInnerMsg(user:string){
        this._innerMsg[user] = 0
        this.executeListener()
    }

    constructor(
        private http:Http,
        private configService:ConfigService
    )
    { }

    //功能：添加用户进聊天用户容器
    addUserToChatUsers(
        user:string
    ){   
        let isExist = false
        for(let obj of this.chatUsers){
            if(obj.userName == user) isExist = true
        }
        if(isExist) return
        this.http.post(this.configService.exportInterface("/getUserInfo"),{user:user})
        .subscribe((res:Response) => {
            let data = res.json().successful[0]
            this.chatUsers.push({
                headImg:data.headImg,
                userName:user,
                isLine:data.isLine
            })
            this.executeListener()     
        })     
    }

    //从聊天用户容器里将用户删除 TODO
    removeUserToChatUsers(user:string){
        for(let obj of this.chatUsers){
            if(obj.userName == user) this.chatUsers.splice(this.chatUsers.indexOf(obj),1)  
        }
        this.executeListener()
    }

    //添加消息进消息容器
    addMsgToMsgCollections(user:string,sendUser:string,msg:string){
        //判断是否存在
        if(!this.msgCollections.hasOwnProperty(user)) {
            this.msgCollections[user] = []
        }
        this.http.post(this.configService.exportInterface("/getUserInfo"),{user:sendUser})
        .subscribe((res:Response) => {
            let data = res.json().successful[0]
            this.msgCollections[user].push({
                headImg:data.headImg,
                msg:msg,
                user:sendUser
            })
            this.executeListener()
        })
    }
    
    //添加监听者
    addListener(listener:Function,isExecute:boolean){
        if(this.listeners.indexOf(listener) == -1) this.listeners.push(listener)
        if(isExecute) listener()
    }
    //遍历监听者执行
    executeListener(){
        for(let listener of this.listeners){
            listener()
        }
    }

    //获取用户容器
    getchatUsers(){
        return this.chatUsers
    }

    //获取聊天容器
    getMsgCollections(){
        return this.msgCollections
    }
}


