import { SubscribeCollection } from './../services/subscribe-collection';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogClose } from './../dialog/dialog-close';
import { DialogModelSchema } from './../interfaces/dialog-schema.interface';
import { ChatService } from './../services/chat.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { MsgModel } from '../interfaces/msg-dto.interface';
import { User } from '../services/user.service';
import { TagContentType } from '@angular/compiler';
import { ActivityStack } from '../services/activity-stack.service';
import { Location } from "@angular/common"
import { Http, Response } from '@angular/http';
import { ConfigService } from '../services/config.service';
import { ReadyQueue } from '../interfaces/ready-queue.interface';
import { Organize2 } from '../services/organize2.service';
import { Msg2Service } from '../services/msg2.service';

@Component({
    selector: 'app-main',
    templateUrl: './main.component.html',
    styleUrls: ['./main.component.css']
})
export class MainComponent extends DialogClose implements OnInit, OnDestroy {

    //是否显示准备面板
    isReadyPaneView: boolean = false
    //用于准备面板显示的队列对象
    readyQueue: { headImg: string, user: string }[] = null

    constructor(
        private chatService: ChatService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private subscribeCollection: SubscribeCollection,
        private user: User,
        private stack: ActivityStack,
        private location: Location,
        private http: Http,
        private configService: ConfigService,
        private organizeService: Organize2,
        private msg2Service:Msg2Service
    ) {
        super();
        //连接服务器
        chatService.connect();

        //更新信息
        this.user.updateUserProperty(true)
    }

    ngOnInit() {

        //栈清空
        this.stack.reset()
        //url进栈
        this.stack.pushURL(this.location.path())

        //接收其它用户的消息
        this.chatService.getMsg()
            .subscribe((data: { msg: MsgModel, tag: string }) => {
                console.log(`接收到其它用户的消息 :${JSON.stringify(data)}`)
                //组队请求
                if (data.tag == "organizeRequest") {
                    this.showDialog({
                        title: "提示信息",
                        message: `${data.msg.username} 邀请你进入队伍`,
                        buttonIndex: 2,
                        btn1: {
                            title: '接受', color: "green", func: () => {
                                console.log("接受好友请求")
                                this.organizeService.init(data.msg.body as any)
                                this.router.navigate(["friend-game"], { relativeTo: this.activatedRoute })
                                this.organizeService.addUser(() => this.router.navigate(["friend-game"], { relativeTo: this.activatedRoute }))
                            }
                        },
                        btn2: {
                            title: '拒绝', color: 'red', func: () => {
                                //拒绝好友请求             
                                this.user.sendMsg(data.msg.username, "拒绝", "organizeRequestRefuse")
                            }
                        }
                    })
                    return
                }

                //进入准备队列
                if (data.tag == "readyQueue") {
                    console.log(data.msg.body)
                    this.chatService.roomID = data.msg.body.roomID
                    let rq: ReadyQueue[] = data.msg.body.queue
                    let readyCount = 0
                    //拼接headImg
                    for (let v of rq) {
                        v.headImg = this.configService.exportInterface(v.headImg)
                        if (v.isReady) {
                            readyCount++
                        }
                    }

                    this.readyQueue = rq
                    this.chatService.readyQueue = rq
                    //显示准备面板
                    if (!this.isReadyPaneView) this.isReadyPaneView = true

                    //准备人数满，隐藏匹配面板
                    if (readyCount == rq.length) {
                        this.isReadyPaneView = false
                        return
                    }
                }

                //组队拒绝
                if (data.tag == "organizeRequestRefuse") {
                    this.basicDialog(`${data.msg.username} 拒绝了你的组队申请`)
                    return
                }

                //好友添加成功
                if (data.tag == "addFriendSuccess") {
                    this.basicDialog("添加成功")
                    return
                }

                //拒绝添加
                if (data.tag == "refuseAddFriend") {
                    this.basicDialog(`${data.msg.username} 拒绝了你的好友申请`)
                    return
                }

                //添加好友
                if (data.tag == "addFriend") {
                    this.showDialog({
                        title: "提示信息",
                        message: `${data.msg.username} 请求添加你为好友`,
                        buttonIndex: 2,
                        btn1: {
                            title: '接受', color: "green", func: () => {
                                //接受好友请求
                                this.user.addFriendTrue(data.msg.username)
                            }
                        },
                        btn2: {
                            title: '拒绝', color: 'red', func: () => {
                                //拒绝好友请求        
                                this.user.sendMsg(data.msg.username, "拒绝", "refuseAddFriend")
                            }
                        }
                    })
                    return
                }

                //获取消息
                //获取用户消息
                if(data.tag == "sendMsg" && window.location.pathname != '/main/msg-pane'){
                    this.msg2Service.addUserToChatUsers(data.msg.username)
                    this.msg2Service.addMsgToMsgCollections(data.msg.username,data.msg.username,data.msg.body)
                    this.msg2Service.selectedUserName = data.msg.username

                    //增加未读消息
                    this.msg2Service.addInnerMsg(data.msg.username)
                }
            })

        //接收用户请求
        this.subscribeCollection.addSubscription("getRequest", this.chatService.getRequest()
            .subscribe((data: any) => {
                this.showDialog({
                    title: "提示信息",
                    message: `${data.user}邀请你加入队伍一起游戏`,
                    buttonIndex: 2,
                    btn1: {
                        title: '接受', color: "green", func: () => {
                            this.router.navigate(['main/friend-game'], { queryParams: { first: data.first } })
                            this.close();
                        }
                    },
                    btn2: {
                        title: '拒绝', color: "red", func: () => {
                            this.close();
                        }
                    }
                })
            }))

    }


    ngOnDestroy() {
        this.subscribeCollection.unSubscription("getMsg");
        this.subscribeCollection.unSubscription("getRequest");
    }

}



