import { SubscribeCollection } from './../../../services/subscribe-collection';
import { User } from './../../../services/user.service';
import { Component, OnInit, OnChanges, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Response } from '@angular/http';
import { ActivityStack } from '../../../services/activity-stack.service';
import { Location } from "@angular/common";
import { Msg2Service } from '../../../services/msg2.service';

@Component({
    selector:'friend-list',
    templateUrl:"./friend-list.component.html",
    styleUrls:["./friend-list.component.css"]
})
export class FriendListComponent implements OnInit, OnDestroy{
    //用于储存获得的好友列表
    friendList:Array<Object>;
    //是否显示好友列表
    isViewFriendList:boolean;
    //是否显示搜索好友框
    isViewSearchFriend:boolean = false;
    //无好友时标签信息
    pName:string = "暂无好友信息， 快去添加好友吧！";
    //输入框信息
    searchValue:string;

    constructor(
        private router:Router,
        private http:Http,
        private user:User,
        private subscribeCollection:SubscribeCollection,
        private stack:ActivityStack,
        private location:Location,
        private msg2Service:Msg2Service
    ){
        //
    }

    //获取好友列表
    getFriendList(){
        let targetUser = this.user.getUserInfo().user;
        //获取好友列表
        this.subscribeCollection.addSubscription("getFriendList",this.user.getFriendList()
        .subscribe(data => {
            if(data.length == 0){
                this.isViewFriendList = false;
                this.friendList = [];
            }
            else{
                this.isViewFriendList = true;
                this.friendList = data;
            }
        }));
    }

    //初始化钩子
    ngOnInit(){
        console.log(this.stack.getStack())
        //获取好友列表
        this.getFriendList();
        //压入执行栈
        this.stack.pushURL(this.location.path())
    }

    //返回上一级
    back(){
        //url出栈
        this.stack.popURL()
        //访问栈顶路由
        this.router.navigate([this.stack.peek()]);
    }

    //显示搜索好友框
    viewSearchFriend(){
        this.isViewSearchFriend = true;
    }

    //隐藏搜索好友框
    hiddenSearchFriend(){
        this.ngOnInit();
        this.isViewSearchFriend = false;
    }

    //聊天点击按钮
    chatFriend(userName:string){
        //添加用户进chatUsers
        this.msg2Service.addUserToChatUsers(userName)
        this.msg2Service.selectedUserName = userName
        //导航至联系面板
        this.router.navigate(['main/msg-pane']);
    }

    ngOnDestroy(){
        this.subscribeCollection.unSubscription('getFriendList');
    }

    refresh(event:Event){
        let degIndex = 360;
        let target:any = event.target;
        target.style.transform = `rotate(360deg)`;
        setTimeout(() => {
            target.style.transform = `rotate(0deg)`;
        },500);
        this.getFriendList();
    }
}


