import { SubscribeCollection } from './../services/subscribe-collection';
import { User } from './../services/user.service';
import { ChatService } from './../services/chat.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit{
  title = 'app works!';
  entryIsView:boolean = false;

  btnName:string;
  constructor(
      private chatService:ChatService,
       private user:User,
       private subscribeCollection:SubscribeCollection
    ){
    //断开连接
    this.chatService.disconnect();

    this.subscribeCollection.addSubscription("indexSetFreeState",this.user.setFreeState(true)
    .subscribe(() => {
      console.log("update user free state");
    }));
    console.log(localStorage.getItem("first"),localStorage.getItem("user"));
  }

  ngOnInit(){

  }

  //显示entryBox
  viewEntryBox(){
    this.entryIsView = true;
  }
  //隐藏entryBox
  hiddenEntryBox(){
    this.entryIsView = false;
  }

  //更改登录注册框的按钮名称
  changeBtnName(btnName:string){
    this.btnName = btnName;
  }
}



