import { Pipe, PipeTransform } from "@angular/core";
import { Organize2User } from "../interfaces/organize-user.interface";
import { ConfigService } from "../services/config.service";

//补齐organizeArr
function completedOrganizeArr(organizeArr:Organize2User[], configService:ConfigService){
    let tmp = configService.MAX_ORGANIZE_USER_COUNT - organizeArr.length
    for(let i = 0;i < tmp;i++){
        organizeArr.push({
            user:"",
            headImg:""
        })
    }
    
    return organizeArr
}

@Pipe({ name: "FriendGamePipe" })
export class FriendGamePipe implements PipeTransform {
    
    constructor(
        private configService:ConfigService
    ){

    }

    transform(value:Organize2User[]) {
        return completedOrganizeArr(value, this.configService)
    }
}


