import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from "@angular/core";
import { ActivityStack } from "../services/activity-stack.service";
import { Location } from "@angular/common";
import { Msg2Service } from "../services/msg2.service";
import { Router } from "@angular/router";
import { SubscribeCollection } from "../services/subscribe-collection";
import { ChatService } from "../services/chat.service";
import { MsgModel } from "../interfaces/msg-dto.interface";
import { chatUser } from "../interfaces/chat-user-schema.interface";
import { Msg } from "../interfaces/msg.interface";
import { HeadImgPipe } from "../pipes/headImgPipe.pipe";
import { User } from "../services/user.service";


@Component({
    selector:"msg-pane",
    templateUrl:"./msg-pane.component.html",
    styleUrls:['./msg-pane.component.css']
})
export class MsgPaneComponent implements OnInit, OnDestroy{
    
    //聊天的用户容器
    private chatUsers:chatUser[] = []

    //聊天消息容器
    private msgCollections:Msg = {}

    //当前聊天用户
    private selectedUserName:string = "空气"


    constructor(
        private stack:ActivityStack,
        private location:Location,
        private msg2Service:Msg2Service,
        private router:Router,
        private subscribeCollection:SubscribeCollection,
        private chatService:ChatService,
        private user:User
    ){
        this.msg2Service.addListener(() => {
            //获取聊天的用户
            this.chatUsers = this.msg2Service.getchatUsers()
            //获取消息容器
            this.msgCollections = this.msg2Service.getMsgCollections()
            //更新聊天名称
            this.selectedUserName = this.msg2Service.selectedUserName
        },true)
        //清空未读消息数目
        this.msg2Service.clearUserInnerMsg(this.selectedUserName)
    }

    //初始化钩子
    ngOnInit(){
        //url入栈
        this.stack.pushURL(this.location.path())

        //获取用户消息
        this.subscribeCollection.addSubscription("MsgPaneGetMsg",
        this.chatService.getMsg()
            .subscribe((data:{msg:MsgModel, tag:string}) => {
                
                //获取用户消息
                if(data.tag == "sendMsg"){
                    this.msg2Service.addUserToChatUsers(data.msg.username)
                    this.msg2Service.addMsgToMsgCollections(data.msg.username,data.msg.username,data.msg.body)
                    //如果不在当前用户聊天面板，用户的未读消息数目++
                    if(data.msg.username != this.selectedUserName) this.msg2Service.addInnerMsg(data.msg.username)
                }
            })
        )
    }
    
    @ViewChild("message")
    msgInput:ElementRef
    //发送消息
    sendMsg(msg:string){
        console.log(msg)
        this.msg2Service.addMsgToMsgCollections(this.selectedUserName,this.user.user, msg)
        this.user.sendMsg(this.selectedUserName, msg, "sendMsg")
        //清空输入框
        this.msgInput.nativeElement.value = ""
    }
    
    //改变当前聊天对象
    changeSelectedUserName(user:string){
        this.msg2Service.selectedUserName = user
        this.msg2Service.clearUserInnerMsg(user)
    }

    back(){
        this.stack.popURL()
        this.router.navigate([this.stack.peek()]);
    }

    ngOnDestroy(): void {
        //组件销毁时取消订阅
        this.subscribeCollection.unSubscription("MsgPaneGetMsg")
    }

}


