import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivityStack } from '../services/activity-stack.service';
import { ProductService } from '../services/product.service';
import { SubscribeCollection } from '../services/subscribe-collection';
import { Response } from '@angular/http';
import { User } from '../services/user.service';
import { HeadImgPipe } from '../pipes/headImgPipe.pipe';

@Component({
    selector: 'app-backpack',
    templateUrl: './backpack.component.html',
    styleUrls: ['./backpack.component.css']
})
export class BackpackComponent implements OnInit {
    
    tools:any[] = []

    constructor(
        private location:Location,
        private stack:ActivityStack,
        private productService:ProductService,
        private subscribeCollection:SubscribeCollection,
        private user:User
    ) {
        //入栈
        this.stack.pushURL(this.location.path())
        this.user.updateUserProperty()
        this.user.directPushListener(() => {
            this.tools = this.user.tools
            console.log(this.tools)
        },true)
     }
    
    ngOnInit() {
        
    }
    
}



