import { DialogComponent } from './../../../../dialog/dialog.component';
import { DialogClose } from './../../../../dialog/dialog-close';
import { SubscribeCollection } from './../../../../services/subscribe-collection';
import { Router } from '@angular/router';
import { ChatService } from './../../../../services/chat.service';
import { User } from './../../../../services/user.service';
import { Component, Input, AfterViewInit, OnInit, OnChanges, OnDestroy, Output } from '@angular/core';
import { Http, Response } from '@angular/http';
import { HeadImgPipe } from '../../../../pipes/headImgPipe.pipe';
import { Msg2Service } from '../../../../services/msg2.service';

@Component({
    selector:"friend-info",
    templateUrl:"./friend-info.component.html",
    styleUrls:["./friend-info.component.css"]
})
export class FriendInfoComponent extends DialogClose implements AfterViewInit,OnInit,OnChanges,OnDestroy{
    @Input()
    isViewFriendList:boolean;
    @Input()
    friendList:Array<Object>;
    @Input()
    btnName:string;
    @Input()
    pName:string;
    @Input()
    btnClick:Function;
    @Input()
    searchName:string;
    @Input()
    isLineHidden:boolean = true

    constructor(
        private http:Http,
        private user:User,
        private chatService:ChatService,
        private router:Router,
        private subscribeCollection:SubscribeCollection,
        private msg2Service:Msg2Service
    ){
        super();
    }

    ngOnChanges(){
        console.log(this.friendList)
    }

    ngOnInit(){
        
    }
 
    ngAfterViewInit(){
    }

    ngOnDestroy(){
        this.subscribeCollection.unSubscription("updateFriendList");
        this.subscribeCollection.unSubscription("queryFriendList");
    }
}


