import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { ActivityStack } from '../services/activity-stack.service';
import { Router, ActivatedRoute } from '@angular/router';
import { User } from '../services/user.service';
import { ConfigService } from '../services/config.service';
import { Response } from '@angular/http';
import { DialogClose } from '../dialog/dialog-close';
import { SubscribeCollection } from '../services/subscribe-collection';

@Component({
  selector: 'app-user-page',
  templateUrl: './user-page.component.html',
  styleUrls: ['./user-page.component.css']
})
export class UserPageComponent extends DialogClose implements OnInit, AfterViewInit, OnDestroy {
    //用户头像
    headImg:string = ""
    //用户昵称
    username:string = ""
    //性别
    sex:string = "unknow"
    //年龄
    age:number = null
    //钱
    money:number = 0
    //等级
    level:number

    //性别下拉框内容
    sexArr:{content:string,value:string}[] = [
        {content:"男",value:"boy"},
        {content:"女",value:"girl"},
        {content:"<未设定>",value:"unknow"}
    ]
    
    //信息快照
    objSnapshot = {
        age:null,
        sex:null
    }

    //年龄下拉框
    ageArr:number[] = []

    constructor(
        private location:Location,
        private stack:ActivityStack,
        private router:Router,
        private user:User,
        private configService:ConfigService,
        private subscribeCollection:SubscribeCollection
    ) {
        super();
    }
    
    ngOnInit() {
        //url入栈
        this.stack.pushURL(this.location.path())   
        //更新用户信息
        this.user.directPushListener(() => {
            this.headImg = this.configService.exportInterface(this.user.headImg)
            this.username = this.user.user
            this.sex = this.user.sex
            this.money = this.user.money
            this.age = this.user.age
            this.level = this.user.level
            console.log(this.sex)
        },true)

        //年龄下拉框初始化
        this.ageArr = this.configService.AGE_ARR
    }
    
    ngAfterViewInit(): void {
        //初始化文件快照
        this.objSnapshot = {
            age:this.age,
            sex:this.sex
        }
    }

    //返回上一级
    back(){
        this.stack.popURL()
        this.router.navigate([this.stack.peek()])
    }

    //更新头像
    updateHeadImg(){
        this.router.navigate(['/main/update-head-img'])
    }

    //保存信息
    saveInfo(){
        //是否修改过
        if(
            this.objSnapshot.age == this.age &&
            this.objSnapshot.sex == this.sex
        ){
            this.basicDialog("并没有修改过啊～")
            return
        }
        //通知后端保存信息
        this.subscribeCollection.addSubscription(
            "saveUserInfo",
            this.user.saveUserInfo({
                age:this.age,
                sex:this.sex
            })
            .subscribe((res:Response) => {
                let data = res.json()
                if(data.err) this.basicDialog(data.err)
                else this.basicDialog(data.successful)
                //通知更新
                this.user.updateUserProperty(true)
            })
        )
    }

    //年龄改变
    ageChange(val:number){
        this.age = val
    }
    //性别改变
    sexChange(val:string){
        this.sex = val
    }

    ngOnDestroy(): void {
        //取消订阅
        this.subscribeCollection.unSubscription("saveUserInfo")
    }

}



